.menu-back {
  position: absolute;
  left: 0px;
  top: 0px;
  height: 100%;
  width: 100%;
  .menu-stack {
    padding: 3px;
    position: absolute;
    background-color: #444;
    button.menu {
      text-align: left;
      margin: 0px;
      display: block;
      color: white;
      width: 100%;
      white-space: nowrap;
      .name {
        display: inline-block;
        margin: 0px 10px;
      }
      .with-check {
        display: inline-block;
        width: 24px;
      }
    }
    button.menu.disable {
      color: darkgray;
    }
    div.menu.separator {
      border-bottom: solid 1px #888;
    }
    button.menu:hover {
      background-color: #888;
    }
  }
}


